import * as React from 'react';
import { Box, Button } from '@chakra-ui/core';

export default function Carousel({ children }) {
  return (
    <Box position="relative" w="100%">
      <Box position="relative" overflow="hidden" maxW="100%" w="100%" zIndex="1">
        <Box display="flex">
          {children.map((item) => (
            <Box flex="1" overflow="hidden">
              {item}
            </Box>
          ))}
        </Box>
      </Box>
      <Box position="absolute" top="0" right="0" bottom="0" left="0" w="100%" h="100%" pointerEvents="none">
        <Button
          pointerEvents="auto"
          position="absolute"
          top="50%"
          right="0"
          transform="translateY(-50%) translateX(50%)"
        >
          next
        </Button>
        <Button
          pointerEvents="auto"
          position="absolute"
          top="50%"
          left="0"
          transform="translateY(-50%) translateX(-50%)"
        >
          prev
        </Button>
      </Box>
    </Box>
  );
}
